<template>
    <section class="pc-container pb-4">
        <div class="pcoded-content">
            <div class="page-header">
                <div class="page-block">
                    <div class="row align-items-center">
                        <div class="col-md-12">
                            <div class="page-header-title">
                                <h5 class="m-b-10">Mantenimientos</h5>
                            </div>
                            <ul class="breadcrumb">
                                <li class="breadcrumb-item">
                                    <router-link to="/sisge">Inicio</router-link>
                                </li>
                                <li class="breadcrumb-item">Visualizacion y Registro</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="col-md-4">
                    <div class="row">
                        <div class="card mb-2" :class="[{ 'bg-primary': selectedModule === 'materias' }]">
                            <div class="card-body">
                                <div class="d-flex">
                                    <div class="row" @click="selectModule('materias')" style="cursor: pointer;">
                                        <a href="javascript:void(0)" class="text-dark fs-5 fw-bold"
                                            :class="{ 'text-white': selectedModule === 'materias' }">Materias</a>
                                        <span class="text-secondary"
                                            :class="{ 'text-light': selectedModule === 'materias' }">Lista y registro</span>
                                    </div>
                                    <button class="btn px-3 rounded ml-auto"
                                        :class="{ 'btn-light text-primary': selectedModule === 'materias', 'btn-outline-secondary text-dark': selectedModule !== 'materias' }"
                                        @click="mostrarModalMateria" :disabled="disabledMateria">
                                        <i data-feather="plus"></i>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="card mb-2" :class="[{ 'bg-primary': selectedModule === 'juzgados' }]">
                            <div class="card-body">
                                <div class="d-flex" @click="selectModule('juzgados')" style="cursor: pointer;">
                                    <div>
                                        <a href="javascript:void(0)" class="text-dark fs-5 fw-bold"
                                            :class="{ 'text-white': selectedModule === 'juzgados' }">Órganos
                                            Jurisdiccionales</a>
                                    </div>
                                    <div >
                                        <button class="btn px-3 py-3 rounded ml-auto"
                                            :class="{ 'btn-light text-primary': selectedModule === 'juzgados', 'btn-outline-secondary text-dark': selectedModule !== 'juzgados' }"
                                            @click="mostrarModalJuzgado" :disabled="disabledJuzgado">
                                            <i data-feather="plus"></i>
                                        </button>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>

                    <div class="row">
                        <div class="card mb-2" :class="[{ 'bg-primary': selectedModule === 'pretension' }]">
                            <div class="card-body">
                                <div class="d-flex">
                                    <div class="row" @click="selectModule('pretension')" style="cursor: pointer;">
                                        <a href="javascript:void(0)" class="text-dark fs-5 fw-bold"
                                            :class="{ 'text-white': selectedModule === 'pretension' }">Pretensiones
                                        </a>
                                        <span class="text-secondary"
                                            :class="{ 'text-light': selectedModule === 'pretension' }">Lista y
                                            registro</span>
                                    </div>
                                    <button class="btn px-3 rounded ml-auto"
                                        :class="{ 'btn-light text-primary': selectedModule === 'pretension', 'btn-outline-secondary text-dark': selectedModule !== 'djudiciales' }"
                                        @click="mostrarModalPretensiones" :disabled="disabledPretension">
                                        <i data-feather="plus"></i>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-md-8" style="overflow-y: auto; max-height: 469px;">
                    <Materias v-if="selectedModule === 'materias'" ref="materiasComponent" />
                    <Juzgados v-else-if="selectedModule === 'juzgados'" ref="juzgadosComponent" />
                    <Pretension v-else-if="selectedModule === 'pretension'" ref="pretensionComponent" />
                </div>
            </div>
        </div>
    </section>
</template>
  
<script>
import feather from 'feather-icons';

// import DJudiciales from './General/DJudiciales.vue';
import Juzgados from './General/Juzgados.vue';
import Materias from './General/Materias.vue';
import Pretension from './General/Pretencion.vue';

export default {
    name: "mantenimiento",
    components: {
        Juzgados,
        Materias,
        Pretension
    },

    created() {
        // Obtener el módulo seleccionado desde el almacenamiento local
        // const savedModuleGeneral = localStorage.getItem('selectedModule');
        // console.log('Valor almacenado en localStorage:', savedModuleGeneral);

        // Establecer el módulo seleccionado o utilizar el predeterminado
        this.selectModule('materias');
        console.log('Módulo seleccionado:', this.selectedModule);
    },



    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    data() {
        return {
            selectedModule: null,
            disabledMateria: true,
            disabledJuzgado: true,
            disabledPretension: true
        };
    },

    methods: {
        mostrarModalMateria() {
            const materiasComponent = this.$refs.materiasComponent;
            materiasComponent.mostrarModal();
        },

        mostrarModalJuzgado() {
            const juzgadosComponent = this.$refs.juzgadosComponent;
            juzgadosComponent.mostrarModal();
        },

        mostrarModalJudiciales() {
            const judicialesComponent = this.$refs.judicialesComponent;
            judicialesComponent.mostrarModal();
        },
        mostrarModalPretensiones() {
            const PretensionesComponent = this.$refs.pretensionComponent;
            PretensionesComponent.mostrarModal();
        },

        buttonDisabled(module) {
            if (module === 'materias') {
                this.disabledMateria = false;
                this.disabledJuzgado = true;
                this.disabledJudicial = true;
                this.disabledPretension = true;
            } else if (module === 'juzgados') {
                this.disabledMateria = true;
                this.disabledJuzgado = false;
                this.disabledJudicial = true;
                this.disabledPretension = true;
            }
            else if (module === 'pretension') {
                this.disabledMateria = true;
                this.disabledJuzgado = true;
                this.disabledJudicial = true;
                this.disabledPretension = false;
            }
        },

        selectModule(module) {
            this.selectedModule = module;

            // Guardar el módulo seleccionado en el almacenamiento local
            localStorage.setItem('selectedModule', module);

            this.buttonDisabled(this.selectedModule);
        },
    },
};
</script>